body {
  font-family: 'Tajawal', sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  overflow: hidden;
  background-color: #f8f9fc; }

/* start btnUp*/
#btnUp {
  background-color: #4167d5;
  border-radius: 5px;
  padding: 7px 10px;
  position: fixed;
  bottom: 5%;
  right: 2%;
  transition: all 0.5s ease;
  cursor: pointer;
  z-index: 999999999999999999999999999999; }
  #btnUp i {
    color: #fff;
    font-size: 20px;
    transition: all 0.5s ease; }
  #btnUp:hover i {
    transform: translate(0, 14%); }
  #btnUp:hover {
    background-color: #000; }

/* end btnUp*/
/* start loading section*/
.loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #4167d5;
  z-index: 9999999999999999999999999999; }
  .loading .SpacedDiv {
    height: 27%;
    width: 100%;
    opacity: 0; }
  .loading .spinner {
    margin: 100px auto;
    width: 50px;
    height: 40px;
    text-align: center;
    font-size: 10px; }
  .loading .spinner > div {
    background-color: #fff;
    height: 100%;
    width: 6px;
    display: inline-block;
    -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
    animation: sk-stretchdelay 1.2s infinite ease-in-out; }
  .loading .spinner .rect2 {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s; }
  .loading .spinner .rect3 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s; }
  .loading .spinner .rect4 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s; }
  .loading .spinner .rect5 {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s; }

@-webkit-keyframes sk-stretchdelay {
  0%, 40%, 100% {
    -webkit-transform: scaleY(0.4); }
  20% {
    -webkit-transform: scaleY(1); } }

@keyframes sk-stretchdelay {
  0%, 40%, 100% {
    transform: scaleY(0.4);
    -webkit-transform: scaleY(0.4); }
  20% {
    transform: scaleY(1);
    -webkit-transform: scaleY(1); } }

/* end loading section*/
/*start our-nav*/
.team-nav {
  background-color: #f8f9fc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); }
  .team-nav .container {
    padding: 0px; }
  .team-nav .bar1, .team-nav .bar2, .team-nav .bar3 {
    width: 35px;
    height: 3px;
    background-color: #333;
    margin: 5px 0;
    transition: 0.4s; }
  .team-nav .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px); }
  .team-nav .change .bar2 {
    opacity: 0; }
  .team-nav .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px); }
  .team-nav .navbar-brand img {
    width: 30%;
    height: 30%; }
  .team-nav .navbar-nav .border-hover {
    position: relative; }
    .team-nav .navbar-nav .border-hover:hover::before {
      width: 100%;
      border-radius: 30px; }
    .team-nav .navbar-nav .border-hover::before {
      transition: all 0.5s ease;
      content: "";
      position: absolute;
      bottom: 0;
      left: 0px;
      width: 0%;
      height: 3px;
      background-color: #4167d5; }
  .team-nav .navbar-nav .nav-item .nav-link {
    font-weight: bolder;
